body {
  background: #eee;
  font-family: Roboto, sans-serif;
  margin: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  min-height: 100vh;
}

.logo {
  text-transform: uppercase;
  background-color: #fc4b3b;
  color: white;
  border-radius: 16px;
  user-select: none;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

[data-qa="notification"] {
  background-color: rgba(0, 128, 0, 0.8);
  margin: 5px;
  padding: 20px;
  border-radius: 6px;
  color: #eee;
}

.warning {
  background-color: rgb(207, 45, 45);
}
